body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

input {
  font-size: 20px;
}

.search-btn {
  height: 30px;
  background-color: rgba(255, 177, 99, .52);
  border-radius: 4px;
  font-size: 20px;
}

.container {
  max-width: 1200px;
  background-color: #fff;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.search-form {
  height: 30px;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.photo-card {
  width: calc(33.33% - 10px);
  box-sizing: border-box;
  border-radius: 8px;
  margin-bottom: 10px;
  overflow: hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 260px;
  border-radius: 8px 8px 0 0;
}

.info {
  background-color: #f2f1f1;
  padding: 10px;
}

.info-item {
  color: #000001;
  letter-spacing: .32px;
  margin: 0;
  font-size: 15px;
  font-weight: 200;
  line-height: 1.5;
}

.item {
  color: #2a2a2e;
  font-size: 12px;
  font-style: italic;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

